body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body{
  padding: 60px;
}

input{
  font-size: 1.2rem;
  width: 100%;
  color: grey;
  border: 1px lawngreen solid;
  margin-bottom: 10px;
  outline: none;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 270px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}